import React, { useEffect, useState } from 'react';
import './IoT.scss'
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, LabelList } from 'recharts';
import { objectiveslistApi } from '../../Requests/RequestFactory';
import { DeviceData } from '../../TypesObjectives';


interface IoTProps{
}

const IoT: React.FC<IoTProps> = () => {

  const [deviceData, setDeviceData] = useState<DeviceData[]>([]);

  useEffect(() => {
    getDeviceData();
  }, []);

  const getDeviceData = async () => {

    const data = await objectiveslistApi.getDeviceData('MOBILEDEVICE');

    if(data){
      console.log(data);
      setDeviceData(data);
    }
  }

  const tooltip = ({ active, payload, label }: any) =>{
    if (!active || !payload || payload.length === 0) return null;
    return(
      <div>
        {payload[0].value}
      </div>
    )
  }

  const getChart = (property: string) => {
    return(
      <ResponsiveContainer width="100%" height={200}>
        <LineChart data={deviceData}>
          <div >
            Environmental Sensor Data
          </div>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="DateAdded" tickFormatter={(tick) => new Date(tick).toLocaleTimeString()} />
          <YAxis />
          <Tooltip content={tooltip} />
          <Line type="monotone" dataKey={property} stroke="#8884d8" strokeWidth={2}>
          <LabelList dataKey="DateAdded" content={({ x, y, value }) => (value)}/>
          </Line>
        </LineChart>
      </ResponsiveContainer>
    )
  }

  return (
    <div className='iotContainer'>
      <div className='iotContainerLeft'>
        <div>Ambient Temperature</div>
        {getChart('AmbientTemperature')}
        <div>Prob Temperature</div>
        {getChart('WeakProbTemperature')}
        <div>Acceleration</div>
        {getChart('TotalAcel')}
        <div>Ambient Humidity</div>
        {getChart('AmbientHumidity')}
      </div> 
      <div className='iotContainerRight'>
        <div>Air quality</div>
        {getChart('AirQuality')}
        <div>Uv Light</div>
        {getChart('UVLight')}
        <div>Light</div>
        {getChart('AmbientLight')}
      </div>
    </div>
  )
}

export default IoT